<template>
  <body>
    <div class="centered">
      <v-select
        v-model="chosenClasses"
        :items="classes"
        item-text="name"
        :menu-props="{ maxHeight: '400' }"
        label="Choose your classes"
        multiple
        chips
        return-object
      ></v-select>

      <v-container>
        <v-row no-gutters>
          <v-col v-for="c in chosenClasses" :key="c.name" cols="12" sm="4">
            <v-card class="pa-2" outlined tile>
              <v-list-item-title class="blue darken-1 headline mb-1">
                {{ c.name }}
              </v-list-item-title>
              <v-list-item-title> {{ c.date }}</v-list-item-title>

              <v-list-item
                ><v-list-item-content>
                  <v-time-picker
                    v-if="c.isEditing"
                    format="ampm"
                    scrollable
                    v-model="c.startTime"
                    :disabled="!c.isEditing"
                    :class="{ view: !c.isEditing }"
                  ></v-time-picker>

                  <v-list-item-title v-else>
                    {{ c.startTime }}</v-list-item-title
                  >
                </v-list-item-content>
              </v-list-item>
              <v-list-item-content>
                <v-list-item-title
                  ><v-btn text icon @click="c.isEditing = !c.isEditing">
                    <v-btn v-if="c.isEditing" text icon>Save</v-btn>
                    <v-icon v-else>mdi-pencil</v-icon>
                  </v-btn></v-list-item-title
                >
              </v-list-item-content>

              <v-list-item-title>
                {{ c.mcqInfo }}
              </v-list-item-title>
              <v-list-item-subtitle>
                {{ timePerQ(c.mcqTime, c.mcqQ) }}</v-list-item-subtitle
              >
              <v-text-field
                dense
                class="centered"
                style="width: 50% "
                :step="1"
                type="number"
                v-model.number="c.numChecks"
                placeholder="Enter number of checks"
                label="Number of checks"
              ></v-text-field>

              <v-list disabled>
                <v-list-item-group>
                  <v-list-item class="blue-grey lighten-2"
                    ><v-list-item-content>
                      <v-list-item-title> Time</v-list-item-title>
                    </v-list-item-content>
                    <v-list-item-content>
                      <v-list-item-title> Questions Done</v-list-item-title>
                    </v-list-item-content>
                    <v-list-item-content>
                      <v-list-item-title>Timer</v-list-item-title>
                    </v-list-item-content></v-list-item
                  >
                  <v-list-item
                    ><v-list-item-content>
                      <v-list-item-title> {{ c.startTime }}</v-list-item-title>
                    </v-list-item-content>
                    <v-list-item-content>
                      <v-list-item-title> START</v-list-item-title>
                    </v-list-item-content>
                    <v-list-item-content>
                      <v-list-item-title>START</v-list-item-title>
                    </v-list-item-content></v-list-item
                  >

                  <v-list-item v-for="(item, i) in c.numChecks" :key="i">
                    <v-list-item-content>
                      <v-list-item-title>{{
                        time(
                          Number(c.startTime.split(":")[0]),
                          Number(c.startTime.split(":")[1]),
                          c.mcqTime,
                          c.mcqQ,
                          c.numChecks,
                          i
                        )
                      }}</v-list-item-title>
                    </v-list-item-content>

                    <v-list-item-content>
                      <v-list-item-title>{{
                        qDone(c.mcqQ, c.numChecks, i)
                      }}</v-list-item-title>
                    </v-list-item-content>
                    <v-list-item-content>
                      <v-list-item-title>{{
                        timer(c.mcqTime, c.mcqQ, c.numChecks, i)
                      }}</v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </v-card>
          </v-col>
        </v-row>
      </v-container>

      <v-btn @click="debug"> Debug </v-btn>
    </div>
  </body>
</template>

<script>
export default {
  name: "test",

  data: () => ({
    classes: [
      {
        name: "AP Computer Science A",
        date: "5/18",
        startTime: "1:00",
        mcqInfo: "Section I: 90 minutes, 40 MCQ",
        mcqTime: 90,
        mcqQ: 40,
        numChecks: 4,
        isEditing: false,
      },
      {
        name: "AP Biology",
        date: "5/27",
        startTime: "9:00",
        mcqInfo: "Section I: 90 minutes, 60 MCQ",
        mcqTime: 90,
        mcqQ: 60,
        numChecks: 6,
        isEditing: false,
      },
      {
        name: "AP Statistics",
        date: "6/10",
        startTime: "1:00",
        mcqInfo: "Section I: 90 minutes, 40 MCQ",
        mcqTime: 90,
        mcqQ: 40,
        numChecks: 4,
        isEditing: false,
      },
      {
        name: "AP Allah",
        date: "4/20",
        startTime: "7:00",
        nTime: 7,
        mcqInfo: "Section I: 90 minutes, 60 MCQ",
        mcqTime: 84,
        mcqQ: 46,
        numChecks: 6,
        frqInfo: "Section II: 90 minutes, 6 free-response questions.",
      },
    ],
    chosenClasses: [],
    inset: true,
  }),

  methods: {
    debug() {
      var hr = this.classes[0].startTime.split(":")[0];
      var min = this.classes[0].startTime.split(":")[1];

      console.log(Number(hr) + 1);
      console.log(hr);
      console.log(Number(min) + 13);
      console.log(min);
    },
    timePerQ(time, questions) {
      var min = Math.floor(time / questions);
      var sec = Math.round(((time % questions) / questions) * 60);
      return min + " minutes " + sec + " seconds per question";
    },
    qDone(questions, check, i) {
      return Math.round((questions / check) * (i + 1) * 10) / 10;
    },

    time(hr, min, time, questions, check, i) {
      // hr = Number(hr);
      // min = Number(min);
      var interval = Math.round(
        (time / questions) * this.qDone(questions, check, i) + min
      );

      while (interval >= 60) {
        //console.log("im in");
        interval -= 60;
        hr++;
      }
      if (hr > 12) {
        hr -= 12;
      }
      // console.log("int" + interval);
      // console.log("hour: " + hr + "min: " + interval);

      if (interval < 10) {
        return hr + ":0" + interval;
      } else {
        return hr + ":" + interval;
      }
    },
    timer(time, questions, check, i) {
      var hr = Math.floor(time / 60);
      var min = time % 60;

      var interval = Math.round(
        (time / questions) * this.qDone(questions, check, i)
      );

      min -= interval;
      if (min < 0) {
        hr--;
        min += 60;
      }
      if (min == 0) {
        min = "00";
      } else if (min < 10) {
        min = "0" + min;
      }

      return hr + ":" + min;
    },
  },

  computed: {},
};
</script>

